.email-container {
    height: 100%;
    width: 100%;
    display: flex;
    background-color: #222831;
    font-family: "Roboto", sans-serif;
    bottom: 100%;
    position: relative;
    opacity: 1;
    -webkit-animation: fadeinpls;
    animation: fadeinpls;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1
}


textarea:focus {
    outline: 0
}

div:focus {
    outline: 0 !important
}

textarea {
    background: rgba(1, 1, 1, 0);
    border-image: none;
    border-image: initial;
    color: #fff;
    font-family: Roboto, Helvetica, Arial, sans-serif;
    border: 0;
    border-bottom: 1px solid #fff;
    resize: none;
    width: 100%
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@keyframes fade-in {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@-webkit-keyframes rotateportrait {
    0% {
        -webkit-transform: rotate(-90deg) scale(1.5);
        transform: rotate(-90deg) scale(1.5)
    }

    to {
        -webkit-transform: rotate(0deg) scale(1);
        transform: rotate(0deg) scale(1)
    }
}

@-webkit-keyframes rotatelandscape {
    0% {
        -webkit-transform: rotate(0) scale(1);
        transform: rotate(0) scale(1)
    }

    to {
        -webkit-transform: rotate(-90deg) scale(1.5);
        transform: rotate(-90deg) scale(1.5)
    }
}

.phone-bottom-container {
    left: 0;
    color: #fff;
    width: 100%;
    bottom: 0;
    height: 40px;
    display: flex;
    z-index: 100;
    position: absolute;
    border-top: 1px solid rgba(1, 1, 1, .5);
    min-height: 40px;
    align-items: center;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    justify-content: center;
    background-color: rgba(1, 1, 1, .25)
}

.phone-bottom-container div {
    margin: 0 16px
}

.phone-border-container {
    top: unset;
    left: unset;
    right: 20px;
    width: 280px;
    bottom: 12px;
    height: 652px;
    margin: unset;
    position: absolute;
    -webkit-animation: rotateportrait .5s ease 0s 1 normal none running;
    animation: rotateportrait .5s ease 0s 1 normal none running;
    min-width: 280px;
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
    min-height: 652px;
    transition: bottom .8s ease 0s;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d
}

.phone-border-inner-container {
    top: -8px;
    left: -4px;
    width: 288px;
    height: 668px;
    padding: 10px 6px;
    z-index: 10;
    position: absolute;
    background: #000;
    box-sizing: border-box;
    border-radius: 12px
}

.phone-border-inner-border {
    top: 2px;
    left: 0;
    width: 100%;
    height: calc(100% - 8px);
    content: "";
    z-index: 1;
    position: absolute;
    background: #000;
    border-top: 2px solid #9fa0a2;
    box-shadow: inset 0 0 6px 0 hsla(0, 0%, 100%, .5);
    border-bottom: 2px solid #9fa0a2;
    border-radius: 12px
}

.phone-border-inner-alignment {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    border-radius: 12px
}

.phone-border-inner-white {
    top: -.5%;
    left: 10px;
    width: calc(100% - 20px);
    height: 101%;
    content: "";
    z-index: 5;
    position: absolute;
    box-shadow: inset 0 0 60px 0 #fff, inset 0 0 30px 0 hsla(0, 0%, 100%, .5), 0 0 20px 0 #fff, 0 0 20px 0 hsla(0, 0%, 100%, .5);
    border-radius: 24px;
    pointer-events: none
}


@-webkit-keyframes cometopapa {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100px);
        transform: translateY(-100px)
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
}

@keyframes cometopapa {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100px);
        transform: translateY(-100px)
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
}

@-webkit-keyframes gotomommy {
    0% {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
        height: 60px;
        max-height: 60px
    }

    to {
        opacity: 0;
        -webkit-transform: translateY(-100px);
        transform: translateY(-100px);
        height: 0;
        max-height: 0
    }
}

@keyframes gotomommy {
    0% {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
        height: 60px;
        max-height: 60px
    }

    to {
        opacity: 0;
        -webkit-transform: translateY(-100px);
        transform: translateY(-100px);
        height: 0;
        max-height: 0
    }
}

