    animation: lds-ripple 1.5s cubic-bezier(0, .2, .8, 1) infinite
}

.component-ripple-effect .lds-ripple div:nth-child(2) {
    -webkit-animation-delay: -.75s;
    animation-delay: -.75s
}

@-webkit-keyframes lds-ripple {
    0% {
        top: 36px;
        left: 36px;
        width: 0;
        height: 0;
        opacity: 1
    }

    to {
        top: 0;
        left: 0;
        width: 72px;
