/*Hud Settings*/

* {
    box-sizing: border-box;
}

.MuiFormControl-root,
hr {
    width: 100%;
}

.hud-settings-wrapper {
    color: #fff;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    position: relative;
}

.hud-settings-wrapper>.hud-settings {
    pointer-events: all;
    position: relative;
    width: 45.625vw;
    height: 60.5vh;
    border-radius: 4px;
    /* font-family: "Droid Serif", serif; */
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #ffffff;
    background-color: #20282e;
    display: flex;
    flex-direction: row;
}

.hud-settings-wrapper>.hud-settings>.setting-menu {
    background-color: #30475c;
    width: 30%;
    height: 100%;
    position: relative;
}

.hud-settings-wrapper>.hud-settings>.setting-menu>.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
}

.hud-settings-wrapper>.hud-settings>.setting-menu>.column {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1 1;
}

.hud-settings-wrapper>.hud-settings>.setting-menu>.boxsetting-menu {
    width: 100%;
    height: 12%;
    max-width: 100%;
    max-height: 8%;
    font-size: 20px;
    /* background-color: blueviolet; */
    position: relative;
    cursor: pointer;
    text-align: left;
    display: flex;
    align-items: center;
}

.hud-settings-wrapper>.hud-settings>.setting-menu>.boxsetting-menu>.active {
    background-color: #20282e;
}

.hud-settings-wrapper>.hud-settings>.setting-menu>.boxsetting-menu>.title {
    line-height: 72px;
    margin-bottom: 20px;
    text-align: left;
    margin-left: 5%;
    flex: 1 1;
}

.hud-settings-wrapper>.hud-settings>.settings-panel {
    background-color: #202730;
    width: 70%;
    height: 100%;
    position: relative;
    margin-left: 1%;
    margin-right: 1%;
    overflow: auto;
}

.hud-settings-wrapper>.hud-settings>.settings-panel>.setting-panel {
    width: 100%;
    height: 100%;
    position: relative;
    margin-left: 1%;
    /*overflow: scroll;*/
}

.hud-settings-wrapper>.hud-settings>.settings-panel>.setting-panel .header {
    font-size: 2.2vh;
    font-weight: bold;
    width: 100%;
    margin-top: 1%;
    margin-bottom: -0.5%;
    /*-1%*/
    color: whitesmoke;
}

.hud-settings-wrapper>.hud-settings>.settings-panel>.setting-panel .spacer {
    margin-top: 20px;
    margin-bottom: 20px;
    border-bottom: 2px solid white;
    border-top: 0 !important;
}

.hud-settings-wrapper>.hud-settings>.settings-panel>.setting-panel .row {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.hud-settings-wrapper>.hud-settings>.settings-panel>.setting-panel .column {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1 1;
    width: 100%;
    /*height: fit-content;*/
    height: 100%;
    margin-bottom: 10px;
}

.hud-settings-wrapper>.hud-settings>.settings-panel>.setting-panel .left-menu {
    width: 50%;
    height: 30%;
    position: relative;
}

.hud-settings-wrapper>.hud-settings>.settings-panel>.setting-panel .right-menu {
    width: 50%;
    height: 30%;
    position: relative;
}

.root {
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    position: absolute;
    max-width: 100vw;
    min-width: 100vw;
    max-height: 100vh;
    min-height: 100vh;
    pointer-events: none;
    border: 0px !important;
    margin: 0px !important;
    outline: 0px !important;
    padding: 0px !important;
    overflow: hidden !important;
}

.hud-settings-container {
    color: white;
    width: 800px;
    height: 600px;
    display: flex;
    position: relative;
    user-select: unset;
    pointer-events: all;
    background-color: rgb(48, 71, 94);
}

.hud-settings-sidebar {
    width: 25%;
}

.hud-settings-body {
    flex: 1 1 0%;
    max-height: 600px;
    overflow-y: scroll;
    background-color: rgb(34, 40, 49);
}

.hud-settings-hud-container {
    width: 100%;
    height: 100%;
    display: flex;
    padding: 16px;
    flex-direction: column;
}

.hud-settings-phone-container {
    width: 100%;
    height: 100%;
    display: flex;
    padding: 16px;
    flex-direction: column;
}

.hud-row {
    margin-bottom: 16px;
}

.hud-inner-row {
    display: flex;
    min-height: 48px;
}

.hud-worppperrdvex .Mui-focused,
.hud-worppperrdvex .MuiFormControlLabel-root,
.hud-worppperrdvex .MuiInput-underline:after,
.hud-worppperrdvex .MuiInputAdornment-root {
    color: rgba(255, 255, 255, 0.7) !important;
    border-color: rgba(255, 255, 255, 0.7) !important;
}

.hud-row-double {
    display: flex;
    min-height: 48px;
}

/*Vehicle Hud*/

.vehicleHud {
    left: calc(14.375vw);
    width: 100%;
    bottom: 6vh;
    height: 20vh;
    display: flex;
    position: absolute;
    align-items: flex-end;
}

.vehicleHud>div {
    margin-right: 16px;
}

.vehicleHud p {
    text-align: center;
    text-shadow: rgb(55 71 79) -1px 1px 0px, rgb(55 71 79) 1px 1px 0px, rgb(55 71 79) 1px -1px 0px, rgb(55 71 79) -1px -1px 0px;
    width: 100% !important;
    font-style: normal !important;
    font-family: Arial, Helvetica, sans-serif !important;
    font-weight: 600 !important;
    font-variant: small-caps !important;
    letter-spacing: 0px !important;
    text-transform: none !important;
    text-decoration: none !important;
}

.speedoMeterContainer {
    width: 62px;
    height: 62px;
    position: relative;
    margin-right: 32px;
}

.speedoMeter {
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    position: absolute;
}

.speedoMeterInner {
    width: 62px;
    height: 62px;
    position: relative;
}

.speedoMeterSvg {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
}

.speedoMeterSpeed {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
}

.speedoMeterSpeed p {
    font-size: 20px !important;
}

.speedoMeterMph {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: flex-end;
    justify-content: center;
}

.fuelPump {
    right: -15px;
    width: 32px;
    bottom: -4px;
    height: 32px;
    position: absolute;
}

.fuelPump .svg-inline--fa {
    color: white;
    width: 14px;
    height: 14px;
}

.fuelPumpInner {
    width: 32px;
    height: 32px;
    position: relative;
}

.fuelPumpSvg {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
}

.fuelPumpIcon {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
}

.fuelPumpIcon svg {
    color: rgb(228, 63, 90);
}

.seatBeltIcon {
    width: 32px;
    height: 62px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.seatBeltIcon path {
    stroke: black;
    stroke-width: 4;
}

.engineDamagedIcon {
    width: 32px;
    height: 62px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.engineDamagedIcon path {
    stroke: black;
    stroke-width: 4;
}

.altContainer {
    width: 62px;
    height: 62px;
    position: relative;
}

.altSvg {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
}

.altSpeed {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
}

.altSpeed p {
    font-size: 20px !important;
}

.altAlt {
    width: 100%;
    height: 100%;
    display: flex;
    position: absolute;
    align-items: flex-end;
    justify-content: center;
}

.waypointDistContainer {
    left: 8px;
    width: 100%;
    bottom: 6vh;
    height: 2vh;
    position: absolute;
}

.waypointDistContainer p {
    text-shadow: rgb(55 71 79) -1px 1px 0px, rgb(55 71 79) 1px 1px 0px, rgb(55 71 79) 1px -1px 0px, rgb(55 71 79) -1px -1px 0px;
    width: 100% !important;
    font-style: normal !important;
    font-family: Arial, Helvetica, sans-serif !important;
    font-weight: 600 !important;
    font-variant: small-caps !important;
    letter-spacing: 0px !important;
    text-transform: none !important;
    text-decoration: none !important;
}

