.slideinnotify {
    -webkit-animation-duration: .4s;
    animation-duration: .4s;
    -webkit-animation-name: slidein;
    animation-name: slidein
}

@-webkit-keyframes slidein {
    0% {
        opacity: 0;
        -webkit-transform: translateX(102%);
        transform: translateX(102%)
    }

    70% {
        opacity: 1
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0)
    }
}

@keyframes slidein {
    0% {
        opacity: 0;
        -webkit-transform: translateX(102%);
        transform: translateX(102%)
    }

    70% {
        opacity: 1
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0)
    }
}

.slideoutnotify {
    -webkit-animation-duration: .6s;
    animation-duration: .6s;
    -webkit-animation-name: slideoutnotify;
    animation-name: slideoutnotify
}

@-webkit-keyframes slideoutnotify {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0)
    }

    to {
        opacity: 0;
        -webkit-transform: translateX(102%);
        transform: translateX(102%)
    }
}

@keyframes slideoutnotify {
    0% {
        opacity: 1;
        -webkit-transform: translateX(0);
        transform: translateX(0)
    }

    to {
        opacity: 0;
        -webkit-transform: translateX(102%);
        transform: translateX(102%)
    }
}

@-webkit-keyframes cometopapa {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100px);
        transform: translateY(-100px)
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
}

@keyframes cometopapa {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100px);
        transform: translateY(-100px)
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0)
    }
}

.fade-in {
    -webkit-animation: cometopapa;
    animation: cometopapa;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1
}

.fade-out {
    opacity: 0;
    -webkit-animation: gotomommy;
    animation: gotomommy;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1
}